.pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 30px;

  .prev,
  .next {
    background: #fff;
    border: none;
    padding: 10px;
    color: blue;
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.4);
    margin: 0 10px;
    cursor: pointer;

    .disabled {
      pointer-events: none;
      box-shadow: none;
      color: #999;
    }
  }

  .paginationItem {
    background: #fff;
    border: 2px solid #666;
    padding: 10px 15px;
    border-radius: 50%;
    height: 45px;
    width: 45px;
    position: relative;
    margin: 0 5px;
    cursor: pointer;

    span {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }

  .paginationItem.active {
    border: 1px solid #888;
    color: #888;
    pointer-events: none;
  }

}